import React from 'react';
import InfoBar from './InfoBar.js'
import Table from './Table.js'
import Buttons from './Buttons.js'
import Searchbar from './Searchbar.js'

class User extends React.Component {
   resetCV = (info) => {
    this.setState(prevState => ({
      cvs: info
    }));
    console.log("Reset CVs to " + info);
  }

  render() {
     if (this.props.loginInfo[4] === 1) {
      return (
        <div id="trainee">
          <InfoBar userId={this.props.loginInfo[1]}
          firstName={this.props.loginInfo[1]}
          lastName={this.props.loginInfo[2]}
          role={this.props.loginInfo[5]}
           />
           <Buttons userId={this.props.loginInfo[1]}/> 
          <Table rows={this.props.rows} cvs={this.props.cvs} onClick={this.resetCV} />
        </div>
      );
    } else if (this.props.loginInfo[4] === 2) {
      return (
        <div id="trainer">
          <InfoBar userId={this.props.loginInfo[1]}
          firstName={this.props.loginInfo[1]}
          lastName={this.props.loginInfo[2]}
          role={this.props.loginInfo[5]}
           />
          {/* <Buttons userId={this.props.loginInfo[1]}/> */}
          <Table rows={this.props.rows} cvs={this.props.cvs} onClick={this.resetCV} />
          <Searchbar onSubmit={this.findCv} />
        </div>
      );
    } else if (this.props.loginInfo[4] === 3) {
      return (
        <div id="manager">
          <InfoBar userId={this.props.loginInfo[1]}
          firstName={this.props.loginInfo[1]}
          lastName={this.props.loginInfo[2]}
          role={this.props.loginInfo[5]}
           />
          {/* <Buttons userId={this.props.loginInfo[1]}/> */}
          <Table rows={this.props.rows} cvs={this.props.cvs} onClick={this.resetCV} />
          <Searchbar onSubmit={this.findCv} />
        </div>
      );
    } else if (this.props.loginInfo[4] === 4) {
      return (
        <div id="sales">
          <InfoBar userId={this.props.loginInfo[1]}
          firstName={this.props.loginInfo[1]}
          lastName={this.props.loginInfo[2]}
          role={this.props.loginInfo[5]}
           />
          {/* <Buttons userId={this.props.loginInfo[1]}/> */}
          <Table rows={this.props.rows} cvs={this.props.cvs} onClick={this.resetCV} />
          <Searchbar onSubmit={this.findCv} />
        </div>
      );
    } else if (this.props.loginInfo[4] === 5) {
      return (
        <div id="soft">
          <InfoBar userId={this.props.loginInfo[1]}
          firstName={this.props.loginInfo[1]}
          lastName={this.props.loginInfo[2]}
          role={this.props.loginInfo[5]}
           />
          {/* <Buttons userId={this.props.loginInfo[1]}/> */}
          <Table rows={this.props.rows} cvs={this.props.cvs} onClick={this.resetCV} />
          <Searchbar onSubmit={this.findCv} />
        </div>
      );
    } else if (this.props.loginInfo[4] === 6) {
      return (
        <div id="consultants">
          <InfoBar userId={this.props.loginInfo[1]}
          firstName={this.props.loginInfo[1]}
          lastName={this.props.loginInfo[2]}
          role={this.props.loginInfo[5]}
           />
          {/* <Buttons userId={this.props.loginInfo[1]}/> */}
          <Table rows={this.props.rows} cvs={this.props.cvs} onClick={this.resetCV} />
          <Searchbar onSubmit={this.findCv} />
        </div>
      );
    } else {
      return (
      <div id="noDep">
          <h1>Please log in.</h1>
        </div>
      );
    }
  }
}

export default User;